@font-face {
  font-family: 'Roboto Condensed Bold';
  src: url('../fonts/RobotoCondensed-Bold.ttf');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto Condensed Regular';
  src: url('../fonts/RobotoCondensed-Regular.ttf');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'FuturaPTCond Bold';
  src: url('../fonts/FuturaPTCond-Bold.ttf');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'ZefaniStencil Regular';
  src: url('../fonts/ZefaniStencil-Regular.otf');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'OPTIBodoni-Antiqua';
  src: url('../fonts/OPTIBodoni-Antiqua.otf');
  font-weight: normal;
  font-style: normal;
}

.button {
  display: inline-block;
  box-sizing: border-box;
  padding: 10px 20px;
  border: 1px solid rgba(255, 255, 255, 0.28);
  border-radius: 3px;
  vertical-align: middle;
  max-height: 34px;
  font-family: 'FuturaPTCond Bold';
  color: white;
  font-size: 11px;
  position: relative;
  overflow: hidden;
  transition: 0.3s ease;
  transition-delay: 0.1s;
}

.button:before {
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 0px;
  width: 0px;
  border-radius: 50%;
  position: absolute;
  background: white;
  z-index: -1;
  transition-timing-function: cubic-bezier(0, -0.22, 0, 1);
  transition: 0.8s ease;
}

.button_img {
  margin-right: 16px;
  vertical-align: middle;
  margin-top: -4px;
}

.button_img-content {
  transition: 0.3s ease;
  transition-delay: 0.1s;
}

.button__colored {
  border: none;
  background: rgba(255, 255, 255, 0.08);
}

.button:hover {
  color: black;
}

.button:hover .button_img-content {
  stroke: black!important;
}

.button:hover:before {
  height: 150px;
  width: 150px;
  transition-delay: 0.1s;
}

.cursor-follow {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -30px 0 0 -30px;
  pointer-events: none;
  z-index: 1000;
  background: white;
  mix-blend-mode: difference;
}

* {
  cursor: url(../img/cursor.png), auto;
}

.preloader {
  position: absolute;
  height: 100%;
  width: 100%;
  background: black;
  z-index: 999;
}

.preloader_text {
  font-size: 50px;
  font-family: 'Roboto Condensed Regular';
  font-weight: normal;
  color: white;
  text-align: center;
  display: none;
}

.preloader_logo {
  display: block;
  text-align: center;
  line-height: 100vh;
}

.preloader span {
  display: inline-block;
}

.overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: white;
  z-index: 998;
}

.font-futura {
  font-family: 'FuturaPTCond Bold';
}

.font-zefani {
  font-family: 'ZefaniStencil Regular';
}

.font-roboto-reg {
  font-family: 'Roboto Condensed Regular';
}

.font-roboto-bold {
  font-family: 'Roboto Condensed Bold';
}

.font-bodoni {
  font-family: 'OPTIBodoni-Antiqua';
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.wrapper {
  width: 87%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.hero {
  position: relative;
  height: 100vh;
  width: 100vw;
}

.background {
  height: 100vh;
  width: 100vw;
  position: absolute;
  z-index: -1;
}

.background_right {
  width: 50%;
  height: 100%;
  background: #000;
  position: absolute;
  left: 0;
  z-index: 10;
}

.background_left {
  width: 50%;
  height: 100%;
  background: #c0c4cb;
  position: absolute;
  right: 0;
  z-index: 5;
}

.nav {
  padding-top: 45px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav_left-block {
  display: flex;
  justify-content: space-between;
}

.nav_right-block,
.nav_left-block {
  flex-basis: 210px;
}

.buttons__right {
  margin-left: 2%;
}

.menu {
  display: flex;
  font-size: 16px;
  font-family: 'FuturaPTCond Bold';
}

.menu_item a {
  color: white;
  transition: 0.3s ease;
  padding: 5px 0;
  border-bottom: 2px solid transparent;
}

.menu_item a:hover {
  color: #e4749d;
  border-bottom: 2px solid #e4749d;
}

.menu_item:not(:last-child) {
  margin-right: 30px;
}

.logo {
  font-size: 42px;
  color: white;
  font-family: 'ZefaniStencil Regular';
}

.user-block {
  display: flex;
  align-items: center;
}

.user-block .avatar {
  margin-left: 20px;
}

.avatar_img {
  background: url('../img/avatar.png');
  height: 35px;
  width: 35px;
  border-radius: 50%;
}

.blog {
  position: absolute;
  width: 100%;
  bottom: 0;
}

.blog_posts {
  display: inline-flex;
  background: #000;
  padding: 18px 0px;
}

.post {
  display: flex;
  margin-right: 20px;
}

.post:hover .post_name {
  text-decoration: underline;
}

.post_name {
  font-size: 12px;
  line-height: 1.25;
  color: white;
  display: block;
  max-width: 150px;
  font-family: 'FuturaPTCond Bold';
  transition: 0.3s ease;
}

.post_date {
  color: #43465e;
  font-size: 10px;
  display: block;
  line-height: 13px;
  font-family: 'Roboto Condensed Regular';
}

.post_img {
  width: 43px;
  margin-left: 10px;
  background-size: cover!important;
}

.post:not(:last-child) {
  padding-right: 20px;
  border-right: 1px solid #43465e;
}

.content {
  margin-top: 2.8%;
  position: relative;
}

.content_wrapper {
  height: 70vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}

.content_head,
.content_subhead {
  font-family: 'OPTIBodoni-Antiqua';
  margin: 0;
  padding: 0;
  font-weight: normal;
  display: block;
}

.content_head {
  color: white;
  font-size: 72px;
  text-align: right;
  margin-bottom: 5px;
}

.content_subhead {
  color: #6b6b6b;
  font-size: 56px;
}

.content_rotated {
  transform-origin: 50% 50%;
  transform: rotate(-90deg) translate(0, -225px);
  width: 67vh;
}

.content_main-image {
  position: absolute;
  overflow: hidden;
  height: 67vh;
  width: 540px;
  background: white;
  background-size: cover!important;
  background-position: center center!important;
  left: 50%;
  transform: translate(-50%, 0);
  box-shadow: 10px 10px 83px -19px rgba(0, 0, 0, 0.3);
}

.main-image_overlay {
  height: 100%;
  width: 100%;
  background: black;
}

.slider-controls {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

.slider-controls_button {
  color: white;
  font-size: 12px;
  font-family: 'Roboto Condensed Bold';
}

.slider-controls_button__bottom {
  padding-right: 25px;
}

.slider-controls_button__top {
  padding-left: 25px;
  border-left: 1px solid #6b6b6b;
}

.slide-block {
  position: absolute;
  right: -195px;
  top: 12vh;
}

.slide-block_wrapper {
  position: relative;
}

.slide-block_text {
  font-size: 72px;
  color: black;
  font-weight: normal;
  font-family: 'OPTIBodoni-Antiqua';
}

.slide-block_text__upper {
  position: absolute;
  top: -7.5vh;
  margin-left: 35%;
  transform: translate(-50%, 0);
}

.slide-block_text__lower {
  position: absolute;
  z-index: 100;
  left: -100px;
  top: 10vh;
}

.slide-block_img {
  margin-top: 12vh;
  height: 55vh;
  width: 380px;
  background-repeat: no-repeat !important;
  background-size: contain!important;
  position: relative;
  z-index: 10;
  box-shadow: 10px 10px 83px -19px rgba(0, 0, 0, 0.3);
}

.slide-block_heading {
  font-size: 28px;
  color: white;
  font-weight: normal;
  font-family: 'OPTIBodoni-Antiqua';
  text-align: center;
  padding: 0;
  margin: 15px 0 10px 0;
}

.slide-block_meta {
  display: flex;
  justify-content: center;
  align-items: center;
}

.slide-block_category {
  font-size: 10px;
  font-family: 'Roboto Condensed Bold';
  color: white;
  opacity: 0.8;
  text-transform: uppercase;
}

.slide-block_circle {
  height: 3px;
  width: 3px;
  border-radius: 50%;
  background: white;
  opacity: 0.8;
  margin: 0 10px;
}

.slide-block_date {
  font-family: 'Roboto Condensed Regular';
  font-size: 10px;
  color: white;
  opacity: 0.8;
  text-transform: uppercase;
}

@media screen and (max-height: 860px) {
  .nav {
    padding-top: 20px;
  }

  .content {
    margin-top: 1.5%;
  }

  .content_wrapper {
    height: 75vh;
  }

  .content_head {
    font-size: 50px;
  }

  .content_subhead {
    font-size: 40px;
  }

  .content_rotated {
    width: 76.5vh;
  }
}